<template>
  <div class="photos-container">
    <h2 class="text-h3 mb-6 text-center font-weight-bold text-pink">Nuestros recuerdos en fotos 💖</h2>
    <v-row>
      <v-col
        v-for="photo in photos"
        :key="photo.id"
        cols="12"
        sm="6"
        md="4"
      >
        <v-card class="photo-card" elevation="8">
          <v-img
            :src="photo.url"
            :alt="photo.description"
            aspect-ratio="1"
            class="photo-image"
          >
            <template v-slot:placeholder>
              <v-row class="fill-height ma-0" align="center" justify="center">
                <v-progress-circular indeterminate color="primary"></v-progress-circular>
              </v-row>
            </template>
          </v-img>
          <v-card-text class="text-center">{{ photo.description }}</v-card-text>
        </v-card>
      </v-col>
    </v-row>
  </div>
</template>

<script>
import { computed, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'PhotosPage',
  setup() {
    const store = useStore()

    onMounted(() => {
      store.dispatch('fetchPhotos')
    })

    const photos = computed(() => store.getters.getPhotos)

    return {
      photos
    }
  }
}
</script>

<style scoped>
.photos-container {
  padding: 10px;
}

.photo-card {
  transition: all 0.3s ease;
  overflow: hidden;
  background-color: rgba(255, 255, 255, 0.581); 
  box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
}

.photo-card:hover {
  transform: scale(1.05);
  background-color: rgba(255, 255, 255, 0.9); 
}

.photo-image {
  width: 100%;   
  transition: all 0.3s ease;
}

.photo-card:hover .photo-image {
  transform: scale(1.1);
}
</style>