<template>
  <div class="hero-container">
    <!-- Fondo de estrellas dinámico -->
    <div class="stars-background">
      <div
        v-for="(star, index) in stars"
        :key="index"
        class="star"
        :style="star.style"
      ></div>
    </div>

    <div class="hero-content">
      <h1 class="hero-title">
        <span v-for="(letter, index) in titleText" :key="index" :style="{ animationDelay: `${index * 0.1}s` }">
          {{ letter }}
        </span>
      </h1>
      <v-btn class="hero-button" @click="scrollToMemories"  to="/photos">
        Descubre nuestra historia 💖
      </v-btn>
    </div>
  </div>
</template>

<script>
export default {
  name: "HeroSection",
  data() {
    return {
      titleText: "Desde el primer día hasta hoy, cada momento contigo es un regalo ✨".split(""),
      stars: [], // Array para almacenar las estrellas
    };
  },
  methods: {
    scrollToMemories() {
      const memoriesSection = document.getElementById("memories");
      if (memoriesSection) {
        memoriesSection.scrollIntoView({ behavior: "smooth" });
      }
    },
    generateStars() {
      const starCount = 200; // Número de estrellas
      const stars = [];
      for (let i = 0; i < starCount; i++) {
        const size = Math.random() * 3 + 1; // Tamaño aleatorio entre 1px y 4px
        const x = Math.random() * 100; // Posición horizontal aleatoria
        const y = Math.random() * 100; // Posición vertical aleatoria
        const duration = Math.random() * 5 + 2; // Duración aleatoria del parpadeo
        const delay = Math.random() * 5; // Retardo aleatorio del parpadeo

        stars.push({
          style: {
            width: `${size}px`,
            height: `${size}px`,
            top: `${y}%`,
            left: `${x}%`,
            animationDuration: `${duration}s`,
            animationDelay: `${delay}s`,
          },
        });
      }
      this.stars = stars;
    },
  },
  mounted() {
    this.generateStars(); // Generar estrellas al cargar el componente
  },
};
</script>

<style scoped>
