.hero-title {
  font-size: 3.5rem;
  font-weight: bold;
  max-width: 800px;
  color: white;
  text-shadow: 2px 2px 10px rgba(255, 255, 255, 0.7);
  display: inline-block;
  line-height: 1.6;
  margin-bottom: 30px; /* Espaciado mayor para separar el botón */
}

/* Efecto de aparición para cada letra */
.hero-title span {
  opacity: 0;
  animation: fadeInLetter 0.5s ease forwards;
}

@keyframes fadeInLetter {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

/* Botón interactivo con efecto de brillo y sombra */
.hero-button {
  font-size: 1.5rem;
