  color: white;
  padding: 5px 40px;
  border-radius: 30px;
  margin-top: 20px;
  transition: 0.3s ease;
  box-shadow: 0px 0px 10px rgba(255, 64, 129, 0.7);
  text-transform: uppercase;
  letter-spacing: 2px;
  cursor: pointer;
  transform: translateY(0); 
}

.hero-button:hover {
  transform: scale(1.1) translateY(-5px); 
  box-shadow: 0px 0px 20px rgba(255, 64, 129, 1);
