<template>
  <v-app :theme="theme">
    <div class="moving-background" :class="{ 'dark-background': isDarkTheme }"></div>

    <v-app-bar app :color="appBarColor" dark elevation="4" class="custom-app-bar">
      <v-app-bar-title class="text-h4 font-weight-bold app-title">
        <v-icon large class="mr-2">mdi-heart</v-icon>
        Nuestro Amor
      </v-app-bar-title>
      <v-spacer></v-spacer>
      <v-btn icon @click="toggleTheme">
        <v-icon>{{ isDarkTheme ? 'mdi-weather-sunny' : 'mdi-weather-night' }}</v-icon>
      </v-btn>
    </v-app-bar>

    <v-main>
      <v-container fluid class="pa-0">
        <transition name="fade" mode="out-in">
          <router-view></router-view>
        </transition>
      </v-container>
    </v-main>

    <v-bottom-navigation v-model="activeTab" grow :color="navColor" elevation="8" class="custom-nav">
      <v-btn value="memories" @click="navigate('memories')">
        <v-icon>mdi-heart</v-icon>
        <span>Inicio</span>
      </v-btn>

      <v-btn value="messages" @click="navigate('messages')">
        <v-icon>mdi-message-text</v-icon>
        <span>Mensajes</span>
      </v-btn>

      <v-btn value="photos" @click="navigate('photos')">
        <v-icon>mdi-image</v-icon>
        <span>Fotos</span>
      </v-btn>

      <v-btn value="wishlist" @click="navigate('wishlist')">
        <v-icon>mdi-gift</v-icon>
        <span>Deseos</span>
      </v-btn>

      <v-btn value="countdown" @click="navigate('countdown')">
        <v-icon>mdi-clock-outline</v-icon>
        <span>Cuenta Regresiva</span>
      </v-btn>
    </v-bottom-navigation>
  </v-app>
</template>

<script>
import { ref, watch, computed } from 'vue'
import { useRouter, useRoute } from 'vue-router' // Import useRoute
import { useTheme } from 'vuetify'

export default {
  name: 'App',
  setup() {
    const router = useRouter()
    const route = useRoute() // Get the current route
    const activeTab = ref('memories'); // Fijamos el valor por defecto a 'memories'


    const theme = useTheme()

    const isDarkTheme = computed(() => theme.global.current.value.dark)

    const appBarColor = computed(() => isDarkTheme.value ? 'purple darken-3' : 'pink lighten-1')
    const navColor = computed(() => isDarkTheme.value ? 'purple darken-3' : 'pink lighten-1')

    // Watch the activeTab to navigate when it changes
    watch(activeTab, (newValue) => {
      if (newValue && newValue !== route.name) {
        router.push({ name: newValue });
      }
    });


    const toggleTheme = () => {
      theme.global.name.value = isDarkTheme.value ? 'light' : 'dark'
    }

    // Navigation function for the bottom navigation buttons
    const navigate = (tab) => {
      activeTab.value = tab; // Update activeTab to the selected tab
    }

    return {
      activeTab,
      isDarkTheme,
      toggleTheme,
      appBarColor,
      navColor,
      theme: computed(() => isDarkTheme.value ? 'dark' : 'light'),
      navigate,
    }
  }
}
</script>

<style>
::-webkit-scrollbar {
  width: 12px;
  height: 12px;
}

::-webkit-scrollbar-track {
  background: #9b4d96;
  border-radius: 10px;
}

::-webkit-scrollbar-thumb {
  background: #5f3a8a;
  border-radius: 10px;
  transition: background 0.3s ease;
}

::-webkit-scrollbar-thumb:hover {
  background: #4a148c;
}

.moving-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(45deg, #9b4d96, #e91e63, #ff6ec7);
  background-size: 400% 400%;
  animation: gradientBG 15s ease infinite;
  z-index: -1;
  transition: all 0.5s ease;
}

.dark-background {
  background: linear-gradient(45deg, #5f3a8a, #9b4d96, #4a148c);
}

@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }

  50% {
    background-position: 100% 50%;
  }

  100% {
    background-position: 0% 50%;
  }
}

.fade-enter-active,
.fade-leave-active {
  transition: opacity 0.5s ease;
}

.fade-enter-from,
.fade-leave-to {
  opacity: 0;
}

.v-main {
  background-color: v-bind('isDarkTheme ? "rgba(18, 18, 18, 0.8)" : "rgba(255, 255, 255, 0.8)"');
  transition: background-color 0.5s ease;
}

.custom-app-bar {
  background: v-bind('isDarkTheme ? "linear-gradient(to right, #5f3a8a, #9b4d96)" : "linear-gradient(to right, #ff758c, #ff7eb3)"') !important;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1) !important;
  transition: background 0.5s ease;
}

.app-title {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.3);
}

.custom-nav {
  background: v-bind('isDarkTheme ? "linear-gradient(to right, #5f3a8a, #9b4d96)" : "linear-gradient(to right, #ff758c, #ff7eb3)"') !important;
  border-top-left-radius: 20px;
  border-top-right-radius: 20px;
  transition: background 0.5s ease;
}

.custom-nav .v-btn {
  transition: all 0.3s ease;
}

.custom-nav .v-btn:hover {
  transform: translateY(-5px);
}

.custom-nav .v-btn--active {
  background-color: v-bind('isDarkTheme ? "rgba(255, 255, 255, 0.1)" : "rgba(255, 255, 255, 0.2)"') !important;
  border-radius: 10px;
}

.v-application.v-theme--dark {
  background-color: #121212;
  color: #ffffff;
}
</style>
