<template>
    <div class="countdown-container">
        <v-card class="countdown-card" elevation="12" shaped>
            <v-card-title class="text-h3 text-center pa-7 countdown-title" style="color: aliceblue;">
                Cuenta Regresiva para
                <div class="event-name">{{ eventName }}</div>
            </v-card-title>
            <v-card-text>
                <v-row class="countdown-timer" justify="center" align="center">
                    <v-col v-for="(value, key) in timeLeft" :key="key" cols="12" sm="3" class="text-center">
                        <v-hover v-slot="{ isHovering, props }">
                            <v-card v-bind="props" :elevation="isHovering ? 16 : 4"
                                :class="['countdown-item', isHovering ? 'on-hover' : '']" rounded="xl">
                                <v-card-text>
                                    <div class="text-h2 font-weight-bold">{{ value }}</div>
                                    <div class="text-subtitle-1 mt-2">{{ key }}</div>
                                </v-card-text>
                            </v-card>
                        </v-hover>
                    </v-col>
                </v-row>
            </v-card-text>
        </v-card>
    </div>
</template>

<script>
import { ref, computed, onMounted, onUnmounted } from 'vue'

export default {
    name: 'CountDown',
    props: {
        targetDate: {
            type: String,
            required: true
        },
        eventName: {
            type: String,
            default: 'Nuestro Aniversario'
        }
    },
    setup(props) {
        const now = ref(new Date().getTime())
        const interval = ref(null)

        const timeLeft = computed(() => {
            const difference = new Date(props.targetDate).getTime() - now.value

            if (difference > 0) {
                const days = Math.floor(difference / (1000 * 60 * 60 * 24))
                const hours = Math.floor((difference % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60))
                const minutes = Math.floor((difference % (1000 * 60 * 60)) / (1000 * 60))
                const seconds = Math.floor((difference % (1000 * 60)) / 1000)

                return { Días: days, Horas: hours, Minutos: minutes, Segundos: seconds }
            }

            return { Días: 0, Horas: 0, Minutos: 0, Segundos: 0 }
        })

        onMounted(() => {
            interval.value = setInterval(() => {
                now.value = new Date().getTime()
            }, 1000)
        })

        onUnmounted(() => {
            if (interval.value) clearInterval(interval.value)
        })

        return {
            timeLeft
        }
    }
}
</script>

<style scoped>
.countdown-container {
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
}

.countdown-card {
    background: v-bind('$vuetify.theme.dark ? "linear-gradient(145deg, #2C3E50, #34495E)" : "linear-gradient(145deg, #ff9a9e, #fad0c4)"');
    border-radius: 20px;
    overflow: hidden;
}

.countdown-title {
    background: v-bind('$vuetify.theme.dark ? "rgba(0, 0, 0, 0.3)" : "rgba(255, 255, 255, 0.3)"');
    color: v-bind('$vuetify.theme.dark ? "#FFFFFF" : "#000000"');
    text-shadow: 2px 2px 4px rgba(92, 92, 92, 0.3);
}

.event-name {
    font-size: 1.5rem;
    margin-top: 10px;
    font-style: italic;
}

.countdown-timer {
    padding: 20px 0;
}

.countdown-item {
    background: v-bind('$vuetify.theme.dark ? "rgba(255, 255, 255, 0.1)" : "rgba(255, 255, 255, 0.8)"');
    transition: all 0.3s ease;
    backdrop-filter: blur(10px);
}

.countdown-item.on-hover {
    transform: translateY(-5px);
}

.countdown-item .text-h2 {
    color: v-bind('$vuetify.theme.dark ? "#FFFFFF" : "#FF758C"');
    font-size: 3rem;
    text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.1);
}

.countdown-item .text-subtitle-1 {
    color: v-bind('$vuetify.theme.dark ? "#95A5A6" : "#FF7EB3"');
    font-weight: bold;
    text-transform: uppercase;
    letter-spacing: 1px;
}

@media (max-width: 600px) {
    .countdown-title {
        font-size: 2rem;
    }

    .event-name {
        font-size: 1.2rem;
    }

    .countdown-item .text-h2 {
        font-size: 2rem;
    }
}
</style>