<template>
  <div class="messages-container">
    <v-parallax :src="backgroundImage" :height="300">
      <div class="d-flex flex-column fill-height justify-center align-center text-grey">
        <h1 class="text-h1 font-weight-bold mb-4 text-center">Mensajes</h1>
        <h4 class="subheading text-shadow">Palabras que vienen del corazón</h4>
      </div>
    </v-parallax>

    <v-container fluid>
      <v-row>
        <v-col v-for="message in messages" :key="message.id" cols="12" sm="6" md="4" class="message-col">
          <v-hover v-slot="{ isHovering, props }">
            <v-card v-bind="props" :elevation="isHovering ? 12 : 4"
              :class="['message-card', `message-card-${message.id}`, { 'on-hover': isHovering }]"
              @click="openDialog(message)">
              <v-card-text class="text-h6 font-weight-bold text-center pa-6">
                {{ truncateText(message.text) }}
              </v-card-text>
              <v-overlay :model-value="isHovering" contained class="align-center justify-center">
                <v-btn color="primary" @click.stop="openDialog(message)" class="btn-overlay">
                  Leer más
                </v-btn>
              </v-overlay>
            </v-card>
          </v-hover>
        </v-col>
      </v-row>
    </v-container>

    <v-dialog v-model="dialog" max-width="500px" transition="dialog-transition">
      <v-card v-if="selectedMessage" :color="selectedMessage.color" dark class="cloud-effect">
        <v-card-text class="text-h6 py-4">
          <div>{{ selectedMessage.text }}</div>
          <v-img :src="selectedMessage.Image" height="200" class="mt-4" />
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="white" text @click="dialog = false">
            Cerrar
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { ref, computed, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'MessagesPage',
  setup() {
    const store = useStore()
    const dialog = ref(false)
    const selectedMessage = ref(null)
    const backgroundImage = '/placeholder.svg?height=300&width=1000'

    onMounted(() => {
      store.dispatch('fetchMessages')
    })

    const messages = computed(() => store.getters.getMessages)

    const truncateText = (text) => {
      return text.length > 50 ? text.substring(0, 47) + '...' : text
    }

    const openDialog = (message) => {
      selectedMessage.value = message
      dialog.value = true
    }


    const addNewMessage = () => {
      console.log('Add new message')
    }

    return {
      messages,
      dialog,
      selectedMessage,
      backgroundImage,
      truncateText,
      openDialog,
      addNewMessage
    }
  }
}
</script>

<style scoped>
.messages-container {
  min-height: 100vh;
}

.message-col {
  display: flex;
}

.message-card {
  flex: 1;
  transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
  overflow: hidden;
  position: relative;
  border-radius: 30px;
  box-shadow: 1px 2px 8px rgba(0, 0, 0, 0.1);
}

.message-card:hover {
  transform: translateY(-10px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
}

.message-card::before {
  content: '';
  position: absolute;
  top: -50%;
  left: -50%;
  width: 200%;
  height: 200%;
  background: conic-gradient(from 0deg at 50% 50%,
      #ff758c 0deg,
      #ff7eb3 90deg,
      #ef9a9a 180deg,
      #ce93d8 270deg,
      #ff758c 360deg);
  animation: rotate 4s linear infinite;
  opacity: 0;
  transition: opacity 0.3s ease;
}

.message-card.on-hover::before {
  opacity: 1;
}

.message-card .v-card__text {
  background: rgba(255, 255, 255, 0.8);
  backdrop-filter: blur(5px);
  color: #333;
  z-index: 1;
  position: relative;
  height: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 10px;
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

.v-parallax__content {
  background: rgba(0, 0, 0, 0.4);
  padding: 20px;
  border-radius: 10px;
}

.v-parallax__content h1 {
  text-shadow: 3px 3px 5px rgba(0, 0, 0, 0.7);
}

.v-parallax__content h4 {
  text-shadow: 2px 2px 4px rgba(0, 0, 0, 0.7);
}

.btn-overlay {
  transition: transform 0.3s ease;
}

.btn-overlay:hover {
  transform: scale(1.1);
}

.btn-floating {
  transition: all 0.3s ease;
}

.btn-floating:hover {
  transform: translateY(-10px);
}

.cloud-effect {
  border-radius: 50% 50% 10% 10%;
  animation: cloudAnimation 0.5s ease-out forwards;
}

@keyframes cloudAnimation {
  from {
    transform: scale(0.8);
    opacity: 0;
  }

  to {
    transform: scale(1);
    opacity: 1;
  }
}


.dialog-transition-enter-active,
.dialog-transition-leave-active {
  transition: opacity 0.5s;
}

.dialog-transition-enter,
.dialog-transition-leave-to {
  opacity: 0;
}
</style>
