import { createRouter, createWebHistory } from 'vue-router'
import Memories from '@/components/MemoriesPage.vue'
import Messages from '@/components/MessagesPage.vue'
import Photos from '@/components/PhotosPage.vue'
import Wishlist from '@/components/WishlistPage.vue'
import Countdown from '@/components/CountDown.vue'

const routes = [
  { path: '/', redirect: '/memories' },
  { path: '/memories', name: 'memories', component: Memories },
  { path: '/messages', name: 'messages', component: Messages },
  { path: '/photos', name: 'photos', component: Photos },
  { path: '/wishlist', name: 'wishlist', component: Wishlist },
  { 
    path: '/countdown', 
    name: 'countdown', 
    component: Countdown,
    props: { targetDate: '2025-03-12', eventName: 'Nuestro Aniversario' }
  }
]

const router = createRouter({
  history: createWebHistory(),
  routes
})

router.beforeEach((to, from, next) => {
  const isReload = !from.name && to.name // Verifica si es una recarga
  if (isReload && to.path !== '/memories') {
    next('/memories')
  } else {
    next()
  }
})

export default router