<template>
  <div class="wishlist-container">
    <h2 class="text-h3 mb-6 text-center font-weight-bold text-pink">Lista de Deseos</h2>

    <v-form @submit.prevent="addWish" class="mb-2">
  <v-row>
    <!-- Campo para el nuevo deseo -->
    <v-col cols="12" sm="6" class="py-0">
      <v-text-field
        v-model="newWish.item"
        label="Nuevo deseo"
        outlined
        dense
        hide-details="auto"
        rows="2"
        class="mb-1"
        style="font-size: 0.9em;"
      ></v-text-field>
    </v-col>

    <!-- Campo para la descripción -->
    <v-col cols="12" sm="6" class="py-0">
      <v-textarea
        v-model="newWish.description"
        label="Descripción"
        outlined
        dense
        hide-details="auto"
        rows="1"
        class="mb-1"
        style="font-size: 0.9em;"
      ></v-textarea>
    </v-col>

    <!-- Botón para agregar el deseo -->
    <v-col cols="12" class="py-0 text-center">
      <v-btn type="submit" color="primary" small class="mt-1">
        Agregar deseo
      </v-btn>
    </v-col>
  </v-row>
</v-form>


    <!-- Lista de deseos activos -->
    <v-list class="wishlist">
      <v-list-item v-for="wish in activeWishlist" :key="wish.id" @click="toggleWishItem(wish.id)"
        class="mb-4 wishlist-item">
        <template v-slot:prepend>
          <v-checkbox :model-value="wish.completed" color="success" class="mr-4"></v-checkbox>
        </template>
        <v-list-item-title :class="{ 'text-decoration-line-through': wish.completed }">
          {{ wish.item }}
        </v-list-item-title>
        <v-list-item-subtitle>{{ wish.description }}</v-list-item-subtitle>

        <!-- Botón para eliminar el deseo -->
        <template v-slot:append>
          <v-btn @click.stop="openDeleteDialog(wish.id)" color="red" icon>
            <v-icon>mdi-delete</v-icon>
          </v-btn>
        </template>
      </v-list-item>
    </v-list>

    <!-- Lista de deseos eliminados -->
    <h3 class="text-h5 mt-6 mb-4 text-purple">Deseos Eliminados</h3>
    <v-list class="wishlist">
      <v-list-item v-for="wish in deletedWishlist" :key="wish.id" class="mb-4 wishlist-item deleted-item">
        <v-list-item-title>{{ wish.item }}</v-list-item-title>
        <v-list-item-subtitle>{{ wish.description }}</v-list-item-subtitle>

        <!-- Botón para restaurar el deseo -->
        <template v-slot:append>
          <v-btn @click.stop="restoreWish(wish.id)" color="green" icon>
            <v-icon>mdi-restore</v-icon>
          </v-btn>
        </template>
      </v-list-item>
    </v-list>

    <!-- Modal de confirmación para eliminar -->
    <v-dialog v-model="deleteDialog" max-width="400">
      <v-card>
        <v-card-title class="text-h5">¿Estás seguro?</v-card-title>
        <v-card-text>
          ¿Quieres eliminar este deseo de la lista?
        </v-card-text>
        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="grey" @click="deleteDialog = false">Cancelar</v-btn>
          <v-btn color="red" @click="confirmDelete">Eliminar</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
import { computed, ref, onMounted } from 'vue'
import { useStore } from 'vuex'

export default {
  name: 'WishlistPage',
  setup() {
    const store = useStore()

    onMounted(() => {
      store.dispatch('fetchWishlist')
    })

    const activeWishlist = computed(() => store.getters.getWishlist)
    const deletedWishlist = computed(() => store.getters.getDeletedWishlist)

    // Nueva propiedad para manejar los valores del formulario
    const newWish = ref({
      item: '',
      description: ''
    })

    // Estado para el modal de confirmación
    const deleteDialog = ref(false)
    const wishToDelete = ref(null)

    // Función para abrir el modal de confirmación
    const openDeleteDialog = (id) => {
      wishToDelete.value = id
      deleteDialog.value = true
    }

    // Función para confirmar la eliminación
    const confirmDelete = () => {
      if (wishToDelete.value) {
        store.commit('removeWishItem', wishToDelete.value)
        deleteDialog.value = false
        wishToDelete.value = null
      }
    }

    // Función para restaurar un deseo
    const restoreWish = (id) => {
      store.commit('restoreWishItem', id)
    }

    // Función para agregar un nuevo deseo
    const addWish = () => {
      if (newWish.value.item.trim() && newWish.value.description.trim()) {
        store.commit('addWishItem', newWish.value)
        newWish.value = { item: '', description: '' } // Limpiar el formulario
      }
    }

    // Función para marcar o desmarcar un deseo como completado
    const toggleWishItem = (id) => {
      store.commit('toggleWishItem', id)
    }

    return {
      activeWishlist,
      deletedWishlist,
      newWish,
      deleteDialog,
      openDeleteDialog,
      confirmDelete,
      restoreWish,
      addWish,
      toggleWishItem
    }
  }
}
</script>

<style scoped>
.wishlist-container {
  padding: 20px;
  overflow: hidden;
  /* Evita el desbordamiento */
}

.wishlist {
  background-color: transparent;
  max-height: 400px;
  /* Ajusta este valor según tus necesidades */
  overflow-y: auto;
  /* Scroll interno solo para las listas */
}

.wishlist-item {
  background-color: rgba(255, 255, 255, 0.503);
  border-radius: 8px;
  transition: all 0.3s ease;
  margin-bottom: 8px;
  /* Ajusta el margen inferior */
}

.wishlist-item:hover {
  transform: translateX(10px);
  background-color: rgba(255, 255, 255, 0.9);
}

.deleted-item {
  opacity: 0.6;
  background-color: rgba(255, 0, 0, 0.1);
}

.v-dialog {
  max-height: 90vh;
  /* Limita el alto del modal */
  overflow-y: auto;
  /* Scroll interno si es necesario */
}
</style>